import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import firebase from "firebase/app";
import "firebase/firestore";
import "firebase/auth";
import { useAuthState } from "react-firebase-hooks/auth";

import { Navbar, Main, Login, OnePost, User } from "./routes/hub";
import { updateUser } from "./functions/updateUser";
import { Status } from "./components/hub";
import { useGetPrevPosts } from "./pages/posts/hooks/usePosts";

import "./App.css";

if (!firebase.apps.length) {
  firebase.initializeApp({
    apiKey: "AIzaSyBjfz_a3vEm-JLHGZlYXt0AjWZWS1aYq7s",
    authDomain: "tester02-b908a.firebaseapp.com",
    projectId: "tester02-b908a",
    storageBucket: "tester02-b908a.appspot.com",
    messagingSenderId: "830501078103",
    appId: "1:830501078103:web:8de8da0851e0e154f8cf12",
  });
}
export const db = firebase.firestore();
export const auth = firebase.auth();

export const UserContext = React.createContext<firebase.User>(
  {} as firebase.User
);

const App: React.FC = () => {
  const [user, loading, error] = useAuthState(auth);
  const POSTS = useGetPrevPosts();

  if (error) return <Status content="Error" />;
  if (loading) return <Status content="Loading" />;

  if (!user) return <Login />;
  else updateUser(user);

  return (
    <UserContext.Provider value={user}>
      <Router basename="/shite">
        <Navbar />

        <div className="Container-outer">
          <div className="Container">
            <Switch>
              <Route exact path="/" render={() => <Main POSTS={POSTS} />} />

              <Route
                path="/user/:uid"
                exact
                render={({
                  match: {
                    params: { uid },
                  },
                }) => <User uid={uid} />}
              />

              <Route
                path="/post/:id"
                exact
                render={({
                  match: {
                    params: { id },
                  },
                }) => <OnePost postId={id} />}
              />

              <Route
                path=""
                render={() => <Status content="page not found" />}
              />
            </Switch>
          </div>
        </div>
      </Router>
    </UserContext.Provider>
  );
};

export default App;
